.skills {
    height: 100vh;
    background-color: lightgray;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;

}

.container {
    border: 1px solid;

    display: flex;
    flex-direction: column;
    background-color: navajowhite;
    width: 100%;
}

.imges {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.imges div {
    height: 200px;
    width: 300px;
    margin-top: 50px;

}

.title {
    display: flex;
    justify-content: center;
    font-weight: bold;
    text-align: center;
}

.imgJs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid blue;

    background-color: floralwhite;
}

.imgJs img {
    width: 150px;
    height: 100px;
}

.imgJs div {
    display: flex;
    justify-content: center;
    margin-top: 5px;
    text-align: center;
}

.imgHtml {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid blue;
    background-color: ghostwhite;
    text-align: center;
}

.imgHtml img {
    width: 150px;
    height: 100px;
}

.imgHtml div {
    display: flex;
    justify-content: center;
    margin-top: 5px;
}

.descTitle {
    display: flex;
    justify-content: center;
    border: 1px solid green;
    background-color: lightcyan;
    text-align: center;
}
.descSkillsDesc {
    text-align: center;
}